@import url('https://fonts.googleapis.com/css2?family=Amatic+SC:wght@700&display=swap');

.card-container{
    display: flex;
    flex-wrap: wrap;
    justify-content: space-evenly;
}

.cards{
    padding: 20px;
    background-color: black;
    border-radius: 7px;
    box-shadow: 3px 3px 5px rgba(0,0,0,0.7);
    margin: 1rem;
    /* width: 500px; */
    position: relative;
    overflow: hidden;
  }

  .title {
    font-family: 'Amatic SC', cursive;
    font-size: 36px;
    /* display: flex; */
    padding: 1rem;
    /* align-items: center; */
    /* justify-content: space-between; */
  }

  .title h2{
      margin: 0;
      color: white;
  }

  .cards img {
    object-fit: cover;
    height: 350px;
    border-radius: 7px;
    width: 500px;
    filter: grayscale(0%);
  }
  .cards img:hover{
    filter: grayscale(100%);
  }

  .card-over{
        position: absolute;
        background-color: rgba(0,0,0,0.5);
        color: white;
        bottom: 0;
        left: 0;
        right: 0;
        /* overflow: auto; */
        /* max-height: 100%; */
        padding: 1rem;
        transform: translateY(100%);
        transition: transform 0.7s ease-in-out;
  }

  .cards:hover .card-over {
      transform: translateY(0%);
  }